import React, { useState, useEffect, useRef, } from 'react';
import style from './timer.module.css';

const padNumber = (num, length) => {
  return String(num).padStart(length, '0');
};

const timeFormat = (num) => {
  const min = padNumber(Math.floor(num / 60), 2)
  const sec = padNumber(Math.floor(num % 60), 2)
  return String(min + ":" + sec)
}

const EnsTimer = () => {
  const [custom, setCustom] = useState(true)
  const [sidebar, setSidebar] = useState(false)
  //button
  const [newPlay, setNewPlay] = useState(true);
  const [metronome, setMetronome] = useState(false);
  const [playToPause, setPlayToPause] = useState(true);

  // 표시
  const [min, setMin] = useState(padNumber(0, 2));
  const [sec, setSec] = useState(padNumber(0, 2));
  const [elapsed, setElapsed] = useState(0);
  const [round, setRound] = useState(0);
  const [remain, setRemain] = useState(0);
  const [ready, setReady] = useState(true);

  // time
  const _initTime = useRef(0);
  const _intervalTime = useRef(0);
  const _intervalRound = useRef(0);
  const _interval = useRef(null);

  //input
  const [inTime, setInTime] = useState();
  const [inSec, setInSec] = useState();
  const [inRound, setInRound] = useState();

  // audio플레이
  const audioPlay = () => {
    document.getElementById('intervalstartsound').load()
    document.getElementById('intervalstartsound').muted = true
    document.getElementById('intervalstartsound').play()

    document.getElementById('intervalstartsound').muted = false
    document.getElementById('intervalstartsound').play()
  }

  const tiktak = () => {
    document.getElementById('tak').load()
    document.getElementById('tak').muted = true
    document.getElementById('tak').play()


    if (!newPlay) {
      if (metronome) {
        document.getElementById('tak').muted = false
        document.getElementById('tak').play()
      }
    }
  }

  // start Time
  const startTime = () => {
    if (inSec > 0 && inTime > 0) {
      setPlayToPause(false);

      if (newPlay) {
        setReady(true);
        setInitTime();
        _intervalTime.current = 10
        setNewPlay(false);
      }
      setInitInterval();

      return () => clearInterval(_interval.current);
    }
  }

  // reset
  const resetTime = () => {
    setNewPlay(true);
    setInitTime();
  }

  //initTime 설정
  const setInitTime = () => {
    setRound(1);
    setSec(padNumber(0, 2));
    setMin(padNumber(parseInt(0), 2));
    setElapsed(0)
    setRemain(0)

    if (inSec >= 1) {
      _initTime.current = inSec * inRound
      _intervalTime.current = inSec
      _intervalRound.current = Math.ceil(_initTime.current / inSec);
    } else {
      _initTime.current = inTime * 60
      _intervalTime.current = _initTime.current
    }
  }

  //initialTimer 설정
  const setInitInterval = () => {
    audioPlay()
    tiktak()

    _interval.current = setInterval(() => {
      _initTime.current -= 1;
      _intervalTime.current -= 1;
      setSec(padNumber(_intervalTime.current % 60, 2));
      setMin(padNumber(parseInt(_intervalTime.current / 60), 2));
      setElapsed(_elapsed => _elapsed + 1)
      setRemain(_initTime.current)
    }, 1000);
  }

  //타이머 정지
  const stopTime = () => {
    setPlayToPause(true);
    clearInterval(_interval.current);
  }

  // 초가 변할 때만 실행되는 useEffect
  // initialTime을 검사해서 0이 되면 interval을 멈춘다.
  useEffect(() => {
    if (ready) {
      if (_intervalTime.current <= 0) {
        setReady(false)
        setInitTime();
        audioPlay(0.1);
      } else if (_intervalTime.current <= 2) {
        audioPlay();
      }
    }
    else if (_intervalTime.current === 0 && !newPlay) {
      _intervalRound.current -= 1;

      if (_intervalRound.current <= 0) {
        setNewPlay(true);
        setPlayToPause(true);
        clearInterval(_interval.current);
        audioPlay();
      } else {
        _intervalTime.current = inSec
        setRound(_round => _round += 1)
        setSec(padNumber(_intervalTime.current % 60, 2));
        setMin(padNumber(parseInt(_intervalTime.current / 60), 2));
        audioPlay();
      }
    }

    if (!newPlay) {
      if (_intervalTime.current != inSec)
        tiktak()
    }
  }, [sec, metronome]);

  //Input
  const inputChangeTime = e => {
    setInTime(parseInt(e.target.value))

    if (e.target.value > 0) {
      setInTime(parseInt(e.target.value))
    }
    setNewPlay(true);

    setInRound(null)
    setInSec(null)
  }
  const inputChangeSec = e => {
    setInSec(parseInt(e.target.value))
    setNewPlay(true);

    if (e.target.value > 0) {
      setInRound(Math.ceil((inTime * 60) / e.target.value))
    } else {
      setInSec(0)
    }
  }
  const inputChangeRound = e => {
    setInRound(parseInt(e.target.value))
    setNewPlay(true);

    if (e.target.value > 0) {
      setInSec(Math.ceil((inTime * 60) / e.target.value))
    } else {
      setInRound(0)
    }
  }


  return (
    <div className={style.container}>
      <div>
        <div className={style.content}>
          <div className={style.status}>
            <div className={style.dismenu}>
              <span>
                Menu
              </span>
              <div>
                <div className={style.line}></div>
                <div className={style.line}></div>
                <div className={style.line}></div>
              </div>
            </div>
            <div className={!metronome ? style.metronomeBtn : `${style.metronomeBtn} ${style.metronomeBtnCheck}`}>
              <div
                onClick={() => setMetronome(!metronome)}>
                Metronome
              </div>
            </div>
          </div>

          <div>
            <div style={{ border: "2px solid #444", margin: "1vw", borderRadius: "1vw" }}>
              <div className={style.setbar}>
                <span className={style.setTitle}>
                  Total Time
                </span>
                <input
                  className={style.input}
                  type="Number"
                  name="Min"
                  placeholder='Min'
                  value={inTime || ''}
                  onChange={inputChangeTime}
                />
              </div>
              <div className={style.setbar}>
                <span className={style.setTitle}>
                  Exercise interval
                </span>
                <input
                  className={style.input}
                  type="Number"
                  name="Sec"
                  placeholder='Sec'
                  value={inSec || ''}
                  onChange={inputChangeSec}
                />
              </div>
              <div className={style.setbar}>
                <span className={style.setTitle}>
                  Total Sets
                </span>
                <input
                  className={style.input}
                  type="Number"
                  name="round"
                  placeholder='Round'
                  value={inRound || ''}
                  onChange={inputChangeRound}
                />
              </div>
            </div>
          </div>

          <hr color='#333' />
          <div className={style.timer}>
            {min}:{sec}
          </div>
          <div className={style.status}>
            <div>
              <h3>Elapsed</h3>
              <p>{timeFormat(elapsed)}</p>
            </div>
            <div>
              <h3>Intervals</h3>
              <p>{inRound >= 0 ? Math.ceil(round / 2) + "/" + inRound : '0 / 0'}</p>
            </div>
            <div>
              <h3>Remaining</h3>
              {ready ?
                <p>{min}:{sec} </p> :
                <p>{timeFormat(remain)}</p>}
            </div>
          </div>

          <hr color='#333' />

          <div className={style.status}>
            <div>
              <img
                className={style.btn}
                src={process.env.PUBLIC_URL + '/images/reset.svg'}
                onClick={resetTime}
                alt='reset' />
            </div>
            <div>
              <img
                className={style.btn}
                src={process.env.PUBLIC_URL + '/images/start.svg'}
                onClick={startTime}
                hidden={!playToPause}
                alt='play' />
              <img
                className={style.btn}
                src={process.env.PUBLIC_URL + '/images/pause.svg'}
                onClick={stopTime}
                hidden={playToPause}
                alt='stop' />
            </div>
          </div>
        </div>

        <div>
          <audio id="tik" >
            <source src={process.env.PUBLIC_URL + '/sounds/beep.mp3'} type="audio/mp3" autoPlay muted />
          </audio>
          <audio id="tak" >
            <source src={process.env.PUBLIC_URL + '/sounds/beep.mp3'} type="audio/mp3" autoPlay muted />
          </audio>
          <audio id="intervalstartsound" >
            <source src={process.env.PUBLIC_URL + '/sounds/beep.wav'} type="audio/mp3" autoPlay muted />
          </audio>
          <audio id="intervalsound">
            <source src="https://www.intervaltimer.com/sounds/a6-sine-750ms.mp3" type="audio/mp3" autoPlay muted />
          </audio>
          <audio id="intervalendsound">
            <source src="https://www.intervaltimer.com/sounds/a6-sine-750ms-end.mp3" type="audio/mp3" autoPlay muted />
          </audio>
        </div>
      </div >
    </div >
  );
}

export default React.memo(EnsTimer);
