import style from './App.module.css';
import React, { useState, useEffect, useRef, } from 'react';
import FRTimer from './components/intervalTimer/FRTimer';
import MsmTimer from './components/intervalTimer/MsmTimer';
import CETimer from './components/intervalTimer/CETimer';
import PageNotFound from './components/pageNotFound/PageNotFound';
import EnsTimer from './components/intervalTimer/EnsTimer';
const App = () => {

  const [tabIdx, setTabIdx] = useState(1);

  const tabData = (idx) => {
    switch (idx) {
      //NewVer
      case 1:
        return <MsmTimer />

      case 2:
        return <FRTimer />

      case 3:
        return <FRTimer />

      case 4:
        return <CETimer />

      default:
        break;
    }
  }

  return (
    <div>
      {typeof document.location.ancestorOrigins[0] != "undefined"
        && document.location.ancestorOrigins[0].includes("xingmove.com")
        ? <EnsTimer />
        : <PageNotFound />
      }
    </div >
  );
}

export default React.memo(App);
